* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;

  color: #212121;
  line-height: 1.5;

}



#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list{
  list-style: none;
}

.flag__list{
  margin: 10px;
}

.flag__mini{
  width: 30px;
  margin-right: 20px
}

.flag__info{
  width: 300px;
}

.capital {
  font-weight: bold;
}

.population {
  font-weight: bold;
}

.languages{
font-weight: bold;
} 


